.formItemBar {
  width: 100%;
  height: 2px;
  background: #00d548;
  transition: 0.1s all;
 }
 .input:focus ~ .formItemBar,
 .textarea:focus ~ .formItemBar {
  background: #7d7d7d;
 }
 .label {
  color: #7d7d7d;
  position: absolute;
  top: 3px;
  left: 0;
  transition: 0.2s ease-out all;
  font-size: 16px;
 }
 .input,
 .textarea {
  color: #fff;
  font-size: 16px;
  border: none;
  line-height: 22px;
  width: 100%;
  background: none;
 }
 .textarea {
  resize: none;
  min-height: 150px;
 }
 .input:focus,
 .textarea:focus {
  outline: none;
 }
 .input:focus + .label,
 .textarea:focus + .label {
  top: -22px;
  font-size: 13px;
  color:#00d548;
 }
 .input:not(:placeholder-shown) + .label,
 .textarea:not(:placeholder-shown) + .label {
  top: -22px;
  font-size: 13px;
 }
 
